
@charset "UTF-8";
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
html {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  line-height: 1.426;
}
article h1,
article h2,
article h3,
article h4,
article h5,
article h6 {
  margin: 10px 0 15px 0;
}
article h1,
section h1 {
  font-size: 2em;
}
a,
a:hover,
a:active,
a:focus,
input,
input:hover,
input:active,
input:focus,
select,
select:hover,
select:active,
select:focus,
textarea,
textarea:hover,
textarea:active,
textarea:focus {
  outline: none;
}
button,
button:active,
button:focus,
button:hover {
  outline: none;
}
ul,
ol {
  list-style: none;
}
.icon-img {
  display: inline-block;
  vertical-align: middle;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% auto;
}
.icon-img-search {
  width: .3rem;
  height: .3rem;
  background-image: url("../assets/img/search_03.png");
}
.icon-img-search2 {
  width: .3rem;
  height: .3rem;
  background-image: url("../assets/img/search_04.png");
}
.icon-img-label-v {
  width: .26rem;
  height: .26rem;
  background-image: url("../assets/img/m_block98k_17.png");
}
.icon-img-nav {
  width: .5rem;
  height: .5rem;
  background-image: url("../assets/img/m_block98k_21.png");
}
.icon-img-flash {
  width: .5rem;
  height: .5rem;
  background-image: url("../assets/img/m_block98k_23.png");
}
.icon-img-back {
  width: .21rem;
  height: .34rem;
  background-image: url("../assets/img/back_03.png");
}
html,
body {
  width: 100%;
  height: 100%;
  color: #4e4e4e;
  font-size: .26rem;
  background: #f1f1f1;
}

/* clearfix [清除浮动]*/
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
  /*IE/7/6*/
}
.fl {
  float: left;
}
.fl {
  float: right;
}
.tl {
  text-align: left;
}
.tr {
  text-align: right;
}
.text-ov1 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.text-ov2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.text-ov3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
.text-ov4 {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  overflow: hidden;
}
.img-box {
  display: block;
  overflow: hidden;
}
.img-box img {
    width: 100%;
}
.app-home {
  padding-top: .9rem;
  max-width: 750px;
  margin: 0 auto;
}
.app-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 1.8rem;
  z-index: 99;
}
.app-header .app-logo {
    width: 1.6rem;
    height: .58rem;
    background: #fff;
    margin-right: .12rem;
}
.app-header .app-search {
    height: .58rem;
    -ms-flex-positive: 1;
        flex-grow: 1;
}
.app-header .search-group {
    position: relative;
}
.app-header .search-group input {
      width: 100%;
      height: 100%;
      padding: .15rem .3rem .15rem .8rem;
      border-radius: .29rem;
      background: #f1f1f1;
      font-size: .3rem;
      border: none;
}
.app-header .search-group input:focus {
      padding-left: .3rem;
}
.app-header .search-group input:focus + i {
      display: none;
}
.app-header .search-group .icon-img {
      position: absolute;
      top: .15rem;
      left: .3rem;
}
.app-header-warpper {
  display: -ms-flexbox;
  display: flex;
  height: .9rem;
  width: 100%;
  padding: .15rem .2rem;
  max-width: 750px;
  margin: 0 auto;
  background: #fff;
}
.app-home-menu {
  max-width: 750px;
  margin: 0 auto;
  height: .9rem;
  line-height: .9rem;
  font-size: .32rem;
  background: #fff;
}
.app-home-menu .menu-list {
    display: -ms-flexbox;
    display: flex;
    padding: 0 .2rem;
    white-space: nowrap;
    overflow: hidden;
}
.app-home-menu .list-item {
    position: relative;
    margin-right: .6rem;
}
.app-home-menu .list-item > a {
      color: #4e4e4e;
}
.app-home-menu .list-item > .router-link-active {
      color: #ff6c25;
}
.app-home-menu .list-item > .router-link-active:after {
        position: absolute;
        bottom: .1rem;
        left: 0;
        content: '';
        display: block;
        width: 100%;
        height: .04rem;
        border-radius: .02rem;
        background: #ff6c25;
}



