
.app-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
.app-footer .footer-nav-menu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 1rem;
    max-width: 750px;
    margin: 0 auto;
    background: #fff;
    border-top: 0.02rem solid #e7e7e7;
}
.app-footer .nav-item {
    padding: .1rem;
    text-align: center;
    width: 50%;
    color: #989898;
}
.app-footer .nav-item.router-link-active {
      color: #fc6625;
}



